import React, { useRef } from 'react';
import { motion, useInView } from 'framer-motion';
import { CampEvent } from '../../types/camp';

const colors = [
  'bg-jeju-ocean',
  'bg-ocean-mist',
  'bg-golden-sun',
  'bg-sunset-coral',
  'bg-ocean-sand',
];

interface MusicianGridItemProps {
  name: string;
  index: number;
}

const MusicianGridItem: React.FC<MusicianGridItemProps> = ({ name, index }) => {
  const bgColor = colors[index % colors.length];
  const initials = name.split('').slice(0, 1).join('');

  return (
    <motion.div
      initial={{ opacity: 0, y: 10 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true }}
      transition={{ duration: 0.4, delay: index * 0.05 }}
      className="text-center group"
    >
      <motion.div
        whileHover={{ scale: 1.05 }}
        className={`w-20 h-20 md:w-24 md:h-24 rounded-full ${bgColor} flex items-center justify-center mx-auto mb-3 text-white font-bold text-xl md:text-2xl shadow-md group-hover:shadow-lg transition-shadow`}
      >
        {initials}
      </motion.div>
      <p className="text-sm md:text-base font-medium text-gray-800">{name}</p>
    </motion.div>
  );
};

interface CampMusiciansProps {
  camp: CampEvent;
}

const CampMusicians: React.FC<CampMusiciansProps> = ({ camp }) => {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, margin: "-100px" });

  if (!camp.participants || camp.participants.length === 0) {
    return null;
  }

  return (
    <section className="bg-white py-16 md:py-20" ref={ref}>
      <div className="container mx-auto px-4">
        <motion.div
          initial={{ opacity: 0, y: 20 }}
          animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 20 }}
          transition={{ duration: 0.6 }}
          className="text-center mb-12"
        >
          <h2 className="text-4xl font-serif font-medium mb-4 text-gray-900">
            Ï∞∏Ïó¨ ÎÆ§ÏßÄÏÖò
          </h2>
          <p className="text-lg text-gray-600">
            {camp.participants.length}ÌåÄÏùò ÎÆ§ÏßÄÏÖòÏù¥ Ìï®ÍªòÌñàÏäµÎãàÎã§
          </p>
        </motion.div>

        <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 gap-6 md:gap-8 max-w-5xl mx-auto">
          {camp.participants.map((musician, index) => (
            <MusicianGridItem key={musician} name={musician} index={index} />
          ))}
        </div>
      </div>
    </section>
  );
};

export default CampMusicians;
