import React, { useRef } from 'react';
import { motion, useInView } from 'framer-motion';
import { CampEvent } from '../../types/camp';
import { getCampProgram } from '../../data/campPrograms';

interface TimelineEventItemProps {
  time: string;
  title: string;
  duration?: string;
  artist?: string;
  description?: string;
  index: number;
  isLast: boolean;
}

const TimelineEventItem: React.FC<TimelineEventItemProps> = ({
  time,
  title,
  duration,
  artist,
  description,
  index,
  isLast,
}) => {
  return (
    <motion.div
      initial={{ opacity: 0, x: -20 }}
      whileInView={{ opacity: 1, x: 0 }}
      viewport={{ once: true }}
      transition={{ duration: 0.4, delay: index * 0.05 }}
      className="flex gap-4 md:gap-6"
    >
      {/* Timeline dot and line */}
      <div className="flex flex-col items-center">
        <div className="w-4 h-4 md:w-5 md:h-5 rounded-full bg-jeju-ocean ring-4 ring-ocean-sand" />
        {!isLast && (
          <div className="w-1 h-16 md:h-20 bg-gradient-to-b from-jeju-ocean/50 to-transparent" />
        )}
      </div>

      {/* Event content */}
      <div className="pb-8 md:pb-12">
        <div className="flex flex-col md:flex-row md:items-baseline md:gap-4 mb-2">
          <span className="text-sm md:text-base font-bold text-jeju-ocean">{time}</span>
          {duration && (
            <span className="text-xs md:text-sm text-coastal-gray">({duration})</span>
          )}
        </div>
        <h3 className="text-base md:text-lg font-bold text-gray-900 mb-1">{title}</h3>
        {artist && (
          <p className="text-sm md:text-base text-ocean-mist font-medium mb-1">
            üé§ {artist}
          </p>
        )}
        {description && (
          <p className="text-sm text-gray-600 leading-relaxed">{description}</p>
        )}
      </div>
    </motion.div>
  );
};

interface CampProgramTimelineProps {
  camp: CampEvent;
}

const CampProgramTimeline: React.FC<CampProgramTimelineProps> = ({ camp }) => {
  const ref = useRef(null);
  const isInView = useInView(ref, { once: true, margin: "-100px" });
  const campProgram = getCampProgram(camp.id);

  if (!campProgram || campProgram.events.length === 0) {
    return null;
  }

  return (
    <section className="bg-ocean-sand py-16 md:py-20" ref={ref}>
      <div className="container mx-auto px-4">
        <motion.h2
          initial={{ opacity: 0, y: 20 }}
          animate={isInView ? { opacity: 1, y: 0 } : { opacity: 0, y: 20 }}
          transition={{ duration: 0.6 }}
          className="text-center text-4xl font-serif font-medium mb-12 text-gray-900"
        >
          ÌñâÏÇ¨ ÌîÑÎ°úÍ∑∏Îû®
        </motion.h2>

        <div className="max-w-2xl mx-auto">
          {campProgram.events.map((event, index) => (
            <TimelineEventItem
              key={index}
              {...event}
              index={index}
              isLast={index === campProgram.events.length - 1}
            />
          ))}
        </div>
      </div>
    </section>
  );
};

export default CampProgramTimeline;
